<template>
  <div class="addressmanagement">
    <van-nav-bar title="地址管理" left-text="返回" left-arrow @click-left="back" />
    <div v-if="list.length>0">
      <van-address-list
        v-model="chosenAddressId"
        :list="list"
        default-tag-text="默认"
        @add="goNewlyAddress"
        @edit="editAddress"
      />
    </div>
    <div v-else>
      <van-empty description="您还没有任何收货地址">
        <van-button round type="default" class="bottom-button" @click="goNewlyAddress">前往添加</van-button>
      </van-empty>
    </div>
  </div>
</template>

<script>
import "../assets/less/addressManagement.less";
export default {
  data() {
    return {
      chosenAddressId: "", //选择地址的id
      list: [], //地址列表数据
    };
  },
  created() {
    this.findAddress();
  },
  methods: {
    back() {
      this.$router.go(-1);
    },
    goNewlyAddress() {
      this.$router.push({ name: "NewlyAddress" });
    },
    editAddress(item) {
      this.$router.push({ name: "NewlyAddress", query: { aid: item.id } });
    },
    //查找地址
    findAddress() {
      let tokenString = localStorage.getItem("WTPTK");
      if (!tokenString) {
        this.$notify({
          message: "请先登录",
          color: "#fff",
          background: "#3585f9",
          duration: 800,
        });
        this.$router.push({ name: "Login" });
      }
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });
      this.axios({
        method: "GET",
        url: "/findAddress",
        params: {
          appkey: this.appkey,
          tokenString: tokenString,
        },
      })
        .then((result) => {
          this.$toast.clear();
          if (result.data.code == 20000) {
            result.data.result.map((v) => {
              let address = v.province + v.city + v.county + v.addressDetail;
              this.list.push({
                id: v.aid,
                name: v.name,
                tel: v.tel,
                address,
                isDefault: Boolean(v.isDefault),
              });
            });
          }
        })
        .catch((err) => {
          this.$toast.clear();
        });
    },
  },
};
</script>

<style lang="less" scoped>
</style>